export default `
<script>
{
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    fruits.fill("wdb")
    write(fruits)
}
{
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    fruits.fill("wdb", 1)
    write(fruits)
}
{
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    fruits.fill("wdb", 2, 3)
    write(fruits)
}
</script>
`



